"use client";
import { useState } from "react";

export default function DashboardLayout({ children }) {
  const [showMenu, setShowMenu] = useState(false);

  return (
    <div className="flex flex-col h-screen">
      <header className="bg-blue-100 p-6 shadow-md flex items-center">
        <div className="ml-auto relative">
          {/* 触发按钮 */}
          <button
            className="flex items-center space-x-2 bg-white p-2 rounded-lg"
            onMouseEnter={() => setShowMenu(true)}
            onMouseLeave={() => setTimeout(() => setShowMenu(false), 100)} // 延迟消失，允许鼠标移入弹窗
          >
            <span className="text-sm text-gray-600">Admin</span>
            <img src="/avatar.ico" alt="avatar" className="w-8 h-8 rounded-full" />
          </button>

          {/* 弹窗卡片 */}
          {showMenu && (
            <div
              className="absolute right-0 top-full w-56 bg-white rounded-lg shadow-xl
                         z-50 p-4 space-y-3
                         opacity-0 scale-95 origin-top-right
                         animate-fade-in"
              onMouseEnter={() => setShowMenu(true)}
              onMouseLeave={() => setShowMenu(false)}
            >
              {/* 弹窗内容 */}
              <div className="flex items-center space-x-3">
                <img src="/avatar.ico" alt="avatar" className="w-10 h-10 rounded-full" />
                <span className="font-medium text-gray-800">Admin</span>
              </div>

              <div className="flex flex-col space-y-1 text-sm">
                <a href="/dashboard/account" className="...">Account</a>
                <a href="/dashboard/settings" className="...">Settings</a>
                <button className="...">Log out</button>
              </div>
            </div>
          )}
        </div>
      </header>

      <main className="flex-1 overflow-y-auto p-6">{children}</main>
    </div>
  );
}